`, "",{"version":3,"sources":["webpack://./src/components/Editor/Properties/PropertyTitleTimePicker2.vue"],"names":[],"mappings":";AAwUA;;CAEA,WAAA;AACA;AAEA;CACA,eAAA;AACA","sourcesContent":["<!--\n@author Alireza Jahedi\n  -->\n\n<template>\n\t<div\n\t\tclass=\"property-title-time-picker\"\n\t\t:class=\"{ 'property-title-time-picker--readonly': isReadOnly }\"\n\t>\n\t\t<CalendarIcon\n\t\t\tv-if=\"isReadOnly\"\n\t\t\tclass=\"property-title-time-picker__icon\"\n\t\t\t:size=\"20\"\n\t\t/>\n\n\t\t<div v-if=\"!isReadOnly\" class=\"property-title-time-picker__time-pickers\">\n\t\t\t<!-- start DatePicker -->\n\t\t\t<div>\n\t\t\t\t<span> </span>\n\n\t\t\t\t<input type=\"text\" id=\"start-input\" v-model=\"formattedStart\" />\n\n\t\t\t\t<VuePersianDatetimePicker\n\t\t\t\t\ttype=\"datetime\"\n\t\t\t\t\tv-model=\"formattedStart\"\n\t\t\t\t\tdisplayFormat=\"jYYYY/jMM/jDD HH:mm\"\n\t\t\t\t\tlocale=\"fa\"\n\t\t\t\t\t@change=\"changeStart\"\n\t\t\t\t\tcustom-input=\"#start-input\"\n\t\t\t\t/>\n\t\t\t</div>\n\n\t\t\t<!-- end DatePicker -->\n\t\t\t<div>\n\t\t\t\t<span> </span>\n\t\t\t\t<input type=\"text\" id=\"end-input\" v-model=\"formattedEnd\" />\n\n\t\t\t\t<VuePersianDatetimePicker\n\t\t\t\t\ttype=\"datetime\"\n\t\t\t\t\tv-model=\"formattedEnd\"\n\t\t\t\t\tdisplayFormat=\" jYYYY/jMM/jDD HH:mm \"\n\t\t\t\t\tlocale=\"fa\"\n\t\t\t\t\t@change=\"changeEnd\"\n\t\t\t\t\tcustom-input=\"#end-input\"\n\t\t\t\t/>\n\t\t\t</div>\n\t\t</div>\n\t\t<div\n\t\t\tv-if=\"isReadOnly\"\n\t\t\tclass=\"property-title-time-picker__time-pickers property-title-time-picker__time-pickers--readonly\"\n\t\t>\n\t\t\t<div\n\t\t\t\tclass=\"property-title-time-picker-read-only-wrapper property-title-time-picker-read-only-wrapper--start-date\"\n\t\t\t>\n\t\t\t\t<div class=\"property-title-time-picker-read-only-wrapper__label\">\n\t\t\t\t\t{{ formattedStart }}\n\t\t\t\t</div>\n\t\t\t\t<IconTimezone\n\t\t\t\t\tv-if=\"!isAllDay\"\n\t\t\t\t\t:name=\"endTimezone\"\n\t\t\t\t\t:class=\"{ 'highlighted-timezone-icon': highlightStartTimezone }\"\n\t\t\t\t\t:size=\"20\"\n\t\t\t\t/>\n\t\t\t</div>\n\t\t\t<template v-if=\"!isAllDayOneDayEvent\">\n\t\t\t\t<div>-</div>\n\t\t\t\t<div\n\t\t\t\t\tclass=\"property-title-time-picker-read-only-wrapper property-title-time-picker-read-only-wrapper--end-date\"\n\t\t\t\t>\n\t\t\t\t\t<div class=\"property-title-time-picker-read-only-wrapper__label\">\n\t\t\t\t\t\t{{ formattedEnd }}\n\t\t\t\t\t</div>\n\t\t\t\t\t<IconTimezone\n\t\t\t\t\t\tv-if=\"!isAllDay\"\n\t\t\t\t\t\t:title=\"endTimezone\"\n\t\t\t\t\t\t:class=\"{ 'highlighted-timezone-icon': highlightStartTimezone }\"\n\t\t\t\t\t\t:size=\"20\"\n\t\t\t\t\t/>\n\t\t\t\t</div>\n\t\t\t</template>\n\t\t</div>\n\n\t\t<div v-if=\"!isReadOnly\" class=\"property-title-time-picker__all-day\">\n\t\t\t<NcCheckboxRadioSwitch\n\t\t\t\t:checked=\"isAllDay\"\n\t\t\t\t:disabled=\"!canModifyAllDay\"\n\t\t\t\t@update:checked=\"toggleAllDay\"\n\t\t\t>\n\t\t\t\t{{ $t(\"calendar\", \"All day\") }}\n\t\t\t</NcCheckboxRadioSwitch>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\n// import moment from \"@nextcloud/moment\";\nimport moment from \"moment-jalaali\";\n\nimport DatePicker from \"../../Shared/DatePicker.vue\";\nimport IconTimezone from \"vue-material-design-icons/Web.vue\";\nimport CalendarIcon from \"vue-material-design-icons/Calendar.vue\";\n\nimport { mapState } from \"vuex\";\nimport { NcCheckboxRadioSwitch } from \"@nextcloud/vue\";\nimport VuePersianDatetimePicker from \"vue-persian-datetime-picker\";\n\nexport default {\n\tname: \"PropertyTitleTimePicker\",\n\tcomponents: {\n\t\tDatePicker,\n\t\tIconTimezone,\n\n\t\tCalendarIcon,\n\t\tNcCheckboxRadioSwitch,\n\t\tVuePersianDatetimePicker,\n\t},\n\tprops: {\n\t\t/**\n\t\t * Whether or not the editor is viewed in read-only\n\t\t */\n\t\tisReadOnly: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * Start date of the event\n\t\t */\n\t\tstartDate: {\n\t\t\ttype: Date,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * Timezone of the start date\n\t\t */\n\t\tstartTimezone: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * End date of the event\n\t\t */\n\t\tendDate: {\n\t\t\ttype: Date,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * Timezone of the end date\n\t\t */\n\t\tendTimezone: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * Whether or not the event is all-day\n\t\t */\n\t\tisAllDay: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * Whether or not the user can toggle the all-day property\n\t\t * This is set to false, whenever this event is part of a recurrence-set\n\t\t */\n\t\tcanModifyAllDay: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * The current timezone of the user\n\t\t * This is used to highlight if the event is in a different timezone\n\t\t */\n\t\tuserTimezone: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\t/**\n\t\t * Whether to append the datepickers to body or not.\n\t\t * Necessary in the AppSidebar, otherwise it will be cut off be the\n\t\t * AppSidebar edges.\n\t\t */\n\t\tappendToBody: {\n\t\t\ttype: Boolean,\n\t\t\tdefault: false,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tshowStartTimezone: true,\n\t\t\tshowEndTimezone: true,\n\t\t};\n\t},\n\tcomputed: {\n\t\t...mapState({\n\t\t\tlocale: (state) => state.settings.momentLocale,\n\t\t}),\n\t\t/**\n\t\t * Tooltip for the All-day checkbox.\n\t\t * If the all-day checkbox is disabled, this tooltip gives an explanation to the user\n\t\t * why it is disabled\n\t\t *\n\t\t * @return {string|null}\n\t\t */\n\t\tallDayTooltip() {\n\t\t\tif (this.canModifyAllDay) {\n\t\t\t\treturn null;\n\t\t\t}\n\t\t\tif (this.isReadOnly) {\n\t\t\t\treturn null;\n\t\t\t}\n\n\t\t\treturn this.$t(\n\t\t\t\t\"calendar\",\n\t\t\t\t\"Cannot modify all-day setting for events that are part of a recurrence-set.\"\n\t\t\t);\n\t\t},\n\t\t/**\n\t\t *\n\t\t * @return {string}\n\t\t */\n\t\tformattedStart() {\n\t\t\tif (this.isAllDay) {\n\t\t\t\t// return moment(this.startDate).locale(this.locale).format(\"ll\"); //old\n\t\t\t\treturn moment(this.startDate).format(\"jYYYY/jM/jD\"); //moment jalaali\n\t\t\t}\n\n\t\t\treturn moment(this.startDate).format(\"jYYYY/jM/jD HH:mm\"); //moment jalaali\n\t\t\t// return moment(this.startDate).locale(this.locale).format(\"lll\"); //old\n\t\t},\n\t\t/**\n\t\t *\n\t\t * @return {string}\n\t\t */\n\t\tformattedEnd() {\n\t\t\tif (this.isAllDay) {\n\t\t\t\t// return moment(this.endDate).locale(this.locale).format(\"ll\"); //old\n\t\t\t\treturn moment(this.endDate).format(\"jYYYY/jM/jD\"); //moment jalaali\n\t\t\t}\n\n\t\t\treturn moment(this.endDate).format(\"jYYYY/jM/jD HH:mm\"); //moment jalaali\n\t\t\t// return moment(this.endDate).locale(this.locale).format(\"lll\"); //old\n\t\t},\n\t\t/**\n\t\t * @return {boolean}\n\t\t */\n\t\thighlightStartTimezone() {\n\t\t\treturn this.startTimezone !== this.userTimezone;\n\t\t},\n\t\t/**\n\t\t * @return {boolean}\n\t\t */\n\t\thighlightEndTimezone() {\n\t\t\treturn this.endTimezone !== this.userTimezone;\n\t\t},\n\t\t/**\n\t\t * True if the event is an all day event, starts and ends on the same date\n\t\t *\n\t\t * @return {boolean}\n\t\t */\n\t\tisAllDayOneDayEvent() {\n\t\t\treturn (\n\t\t\t\tthis.isAllDay &&\n\t\t\t\tthis.startDate.getDate() === this.endDate.getDate() &&\n\t\t\t\tthis.startDate.getMonth() === this.endDate.getMonth() &&\n\t\t\t\tthis.startDate.getFullYear() === this.endDate.getFullYear()\n\t\t\t);\n\t\t},\n\t},\n\tmethods: {\n\t\t/**\n\t\t * Update the start date\n\t\t *\n\t\t * @param {Date} value The new start date\n\t\t */\n\t\tchangeStart(value) {\n\t\t\tconst valueToUnix = moment(value).unix();\n\n\t\t\tthis.$emit(\"update-start-date\", moment.unix(valueToUnix).toDate());\n\t\t},\n\t\t/**\n\t\t * Updates the timezone of the start date\n\t\t *\n\t\t * @param {string} value The new start timezone\n\t\t */\n\t\tchangeStartTimezone(value) {\n\t\t\t// If the value didn't change, value is null\n\t\t\tif (!value) {\n\t\t\t\treturn;\n\t\t\t}\n\n\t\t\tthis.$emit(\"update-start-timezone\", value);\n\t\t},\n\t\t/**\n\t\t * Update the end date\n\t\t *\n\t\t * @param {Date} value The new end date\n\t\t */\n\t\tchangeEnd(value) {\n\t\t\tconst valueToUnix = moment(value).unix();\n\n\t\t\tthis.$emit(\"update-end-date\", moment.unix(valueToUnix).toDate());\n\t\t},\n\t\t/**\n\t\t * Updates the timezone of the end date\n\t\t *\n\t\t * @param {string} value The new end timezone\n\t\t */\n\t\tchangeEndTimezone(value) {\n\t\t\t// If the value didn't change, value is null\n\t\t\tif (!value) {\n\t\t\t\treturn;\n\t\t\t}\n\n\t\t\tthis.$emit(\"update-end-timezone\", value);\n\t\t},\n\t\t/**\n\t\t * Toggles the all-day state of an event\n\t\t */\n\t\ttoggleAllDay() {\n\t\t\tif (!this.canModifyAllDay) {\n\t\t\t\treturn;\n\t\t\t}\n\n\t\t\tthis.$emit(\"toggle-all-day\");\n\t\t},\n\t},\n};\n</script>\n<style>\n#start-input,\n#end-input {\n\twidth: auto;\n}\n\n.property-title-time-picker__time-pickers div span {\n\tdisplay: inline;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
